@use "colorVar" as v;

#aboutMe {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-size: cover;
  color: white;
  text-align: left;
  padding-top: 3rem;
  overflow: visible;
  min-height: 100vh;
  background: transparent;
  .fa-rocket {
    margin-right: 0.5em;
    transition: all 0.25s ease-out;
    &:hover {
      text-shadow: 0px 0px 10px rgba(215, 41, 166, 0.95);
      transform: scale(1.1);
    }
  }

  .aboutH2 {
    margin: 0;
    padding: 2rem 5rem 2rem 5rem;

    background-clip: border-box;
    width: 100%;
    font-size: 2.5rem;
    text-align: center;
  }
  .aboutContent {
    display: flex;
    position: relative;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 3rem;
    position: relative;
    width: 100%;
    height: 100%;
    cursor: zoom-in;
    span {
      width: 100%;
    }
    .aboutPara {
      position: relative;
      padding: 3rem;
      width: 80%;
      transition: all 0.25s ease-out;
      border-radius: 5px;
      background-image: linear-gradient(
        40deg,
        hsl(264deg 100% 40%) 0%,
        hsl(265deg 99% 41%) 30%,
        hsl(267deg 99% 42%) 45%,
        hsl(268deg 99% 42%) 56%,
        hsl(269deg 98% 43%) 64%,
        hsl(270deg 98% 44%) 70%,
        hsl(271deg 98% 45%) 76%,
        hsl(272deg 98% 46%) 80%,
        hsl(273deg 98% 47%) 84%,
        hsl(274deg 98% 47%) 88%,
        hsl(274deg 98% 48%) 91%,
        hsl(275deg 98% 49%) 94%,
        hsl(276deg 98% 50%) 97%,
        hsl(276deg 99% 50%) 100%
      );
    }
    .aboutPara:hover {      
      transform: scale(1.02);
      transition: all 0.25s ease-in-out;
    }
  }
}
.light-mode #aboutMe .aboutContent .aboutPara {
  background-color: white;
  background-image: none;
  box-shadow: 0px 5px 20px -15px rgba(0, 0, 0, 0.75);
}
.light-mode #aboutMe .fa-rocket {
  color: #5000ca;
  text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
  &:hover {
  }
}
.section {
  color: #ffffff;
  padding: 2rem;
  border-radius: 8px;
  background: linear-gradient(135deg, #141e30, #243b55); // Fond sombre amélioré
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.5); // Effet d'ombre léger
  transition: all 0.3s ease-in-out;

  &:hover {
    background: linear-gradient(
      135deg,
      #1a253a,
      #2a456b
    ); // Léger effet highlight au survol
  }

  h2 {
    font-size: 1.8rem;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;

    i {
      color: #ffcc00; // Couleur des icônes des titres
    }
  }

  ul {
    list-style: none;
    padding: 0;

    li {
      font-size: 1.2rem;
      display: flex;
      align-items: center;
      gap: 0.7rem;
      margin-bottom: 0.5rem;
      transition: transform 0.2s ease-in-out;

      &:hover {
        transform: scale(1.05);
        color: #ffcc00; // Surbrillance légère au survol
      }
    }
  }

  .contact {
    font-size: 1.2rem;
    margin-top: 1.5rem;
    display: flex;
    align-items: center;
    gap: 0.5rem;
    background: rgba(255, 255, 255, 0.1);
    padding: 0.8rem;
    border-radius: 5px;
    transition: all 0.3s ease-in-out;

    i {
      color: #ff4d4d; // Icône email en rouge doux
    }

    &:hover {
      background: rgba(255, 255, 255, 0.2);
    }
  }
}
.list-container {
  display: flex;
  justify-content: space-evenly;
  gap: 3rem;
  background-color: #1a253a;

  padding: 3rem;
  i {
    transition: transform 0.25s ease-out;

    &:hover {
      transform: scale(1.5);
      text-shadow: 0px 0px 10px rgba(241, 226, 13, 0.85);
    }
  }

  .aboutCard {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 40%;
    i {
      margin-right: 0.5rem;
    }
    ul {
      list-style: none;
      padding: 0;
      margin: 0;
      line-height: 2rem;
    }
    .skills {
      display: flex;
      flex-direction: column;
      gap: 10px;
    }
  }
}
.light-mode .list-container {
  background-color: #f8f9fa;
  ul {
    color: black;
  }
  i {
    color: #5000ca;

    &:hover {
      text-shadow: 0px 0px 5px rgba(0, 0, 0, 0.5);
    }
  }
}

@media (max-width: 768px) {
  #aboutMe .aboutH2 {
    font-size: 1.5rem;
  }
  #aboutMe h2 {
    font-size: 1.5rem;
  }
  .list-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    .aboutCard {
      width: 100%;
    }
  }
  #aboutMe .aboutContent {
    height: 100%;
    margin-bottom: 0;
    .aboutPara {
      margin: 0;
      width: 100%;
      border-radius: 0;
    }
  }
}
